<style scoped>
  .profile{
    width:80px;
    height:80px;
    object-fit: cover;
    object-position: center;
  }
</style>

<template>
<div id="bg">

  
    <v-snackbar v-model="snackbar">
        {{ snackbarText }}
      </v-snackbar>

  <!-- Preloader-->
    <div class="preloader" id="preloader" v-if="saveLoading">
      <div class="spinner-grow text-secondary" role="status">
        <div class="sr-only">Loading...</div>
      </div>
    </div>
    <!-- Header Area-->
    <div class="header-area" id="headerArea" v-else>
      <div class="container h-100 d-flex align-items-center justify-content-between">
        <!-- Back Button-->
        <div class="back-button"><router-link to="/profile">
            <svg class="bi bi-arrow-left" xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" viewBox="0 0 16 16">
              <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"></path>
            </svg></router-link></div>
        <!-- Page Title-->
        <div class="page-heading">
          <h6 class="mb-0">Edit Profile</h6>
        </div>
        <div></div>
      </div>
    </div>
    
    <div class="page-content-wrapper">
      <div class="container">
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-3">
          <!-- User Information-->
          <div class="card user-info-card">
            <div class="card-body p-4 d-flex align-items-center">
              <div class="user-profile me-3">

                <img :src="STORAGE_URL+'/public/'+userLoggedIn.alumni.profile_pic" :alt="userLoggedIn.name" v-if="userLoggedIn.alumni.profile_pic" class="profile">
                <img src="../assets/img/bg-img/9.jpg" alt="" v-else>

                <input
                  type="file"
                  style="display: none"
                  ref="image"
                  accept="image/*"
                  @change="onFilePicked">

                <div class="change-user-thumb">
                    <button @click="pickFile"><i class="lni lni-pencil"></i></button>
                </div>

              </div>
              <div class="user-info">
                <p class="mb-0 text-dark">{{userLoggedIn.alumni.email ? userLoggedIn.alumni.email : '-'}}</p>
                <p class="mb-0 text-dark">{{userLoggedIn.alumni.NRA ? userLoggedIn.alumni.NRA : '-'}}</p>
                <h5 class="mb-0">{{userLoggedIn.name}}</h5>
              </div>
            </div>
          </div>

 <!-- Single Setting Card-->
          <div class="card settings-card mt-2">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private Email</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_email">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private No HP</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_phone">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-protection bg-success"></i><span>Private Domisili</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="userLoggedIn.alumni.private_domisili">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <!-- <div class="card settings-card">
            <div class="card-body">
              
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title private"><i class="lni lni-map-marker bg-success"></i><span>Tinggal di luar negeri</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="isForeign">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div> -->

          <!-- User Meta Data-->
          <div class="card user-data-card">
            <div class="card-body">
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-user"></i><span>Full Name</span></div>
                  <input class="form-control" type="text" placeholder="Suha Jannat" v-model="userLoggedIn.name">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-user"></i><span>Kelas</span></div>
                  <input class="form-control" type="text" placeholder="Kelas 2" v-model="userLoggedIn.alumni.class_name">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-phone"></i><span>Phone</span></div>
                  <input class="form-control" type="text" placeholder="+880 000 111 222" v-model="userLoggedIn.email">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-envelope"></i><span>Email Address</span></div>
                  <input class="form-control" type="email" placeholder="care@example.com" v-model="userLoggedIn.alumni.email">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-map-marker"></i><span>Domisili</span></div>
                  <input class="form-control" type="text" placeholder="Negara" v-if="!isForeign" v-model="userLoggedIn.alumni.country">
                  <select class="form-control mt-2" v-model="selectedProvince" @change="changeProvince" v-if="!isForeign">
                    <option value="" disabled>- Pilih Provinsi -</option>
                    <option v-for="province  in provinces" :key="province.province_id" :value="province.province">{{province.province}}</option>
                  </select>
                  <select class="form-control mt-2" v-model="selectedCity" v-if="!isForeign">
                    <option value="" disabled>- Pilih Kabupaten / Kota -</option>
                    <option v-for="city  in cities" :key="city.city_id" :value="city.city_name">{{city.city_name}}</option>
                  </select>
                  <input class="form-control mt-2" type="text" placeholder="Domisili" v-model="userLoggedIn.alumni.address">
                  <!-- <input class="form-control mt-2" type="text" placeholder="Kota" v-if="!isForeign" v-model="userLoggedIn.alumni.city">
                  <input class="form-control mt-2" type="text" placeholder="Provinsi" v-if="!isForeign" v-model="userLoggedIn.alumni.province"> -->
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-calendar"></i><span>Tahun Masuk</span></div>
                  <input class="form-control" type="text" placeholder="2000" v-model="userLoggedIn.alumni.year_in">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-calendar"></i><span>Tahun Lulus</span></div>
                  <input class="form-control" type="text" placeholder="2004" v-model="userLoggedIn.alumni.graduation_year">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-calendar"></i><span>Tanggal Lahir</span></div>
                  <input class="form-control" type="date" v-model="userLoggedIn.alumni.date_of_birth">
                </div>
                <div class="mb-3">
                  <div class="d-flex justify-content-between">
                    <div class="title mb-2">
                      <i class="lni lni-briefcase"></i><span>Pekerjaan / Keahlian</span>
                    </div>
                    <v-btn icon small @click="addSkill">
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                  </div>
                  <div id="skills" v-for="(skill,i) in userLoggedIn.alumni.skills" :key="i">

                    <div class="input-group mb-3">
                      <input class="form-control" type="text" :placeholder="'Pekerjaan / Keahlian ke '+(i+1)" v-model="skill.name">
                      <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="removeSkill(i,skill.id)"><i class="lni lni-close"></i></button>
                      </div>
                    </div>

                  </div>
                </div>
                <button class="btn btn-success w-100 text-white" type="submit" @click="editProfile()">Save All Changes</button>
              
            </div>
          </div>

        </div>
      </div>
    </div>
</div>
</template>

<style scoped>
#bg{
  background: #e5e8ff !important;
}
.private{
  font-size:16px !important;
}
</style>

<script>
import axios from 'axios'
export default {
  name: 'EditProfile',
  data: () => ({
    userLoggedIn:JSON.parse(localStorage.getItem("userLoggedIn")),
    saveLoading:false,
    profileLoading:false,
    snackbar:false,
    snackbarText:'',
    BASE_URL:process.env.VUE_APP_URL,
    STORAGE_URL:process.env.VUE_APP_STORAGE_URL,
    isForeign:false,
    provinces:[],
    cities:[],
    selectedProvince: "",
    selectedCity:"",
  }),
  created(){
     this.getProvinces()
  },
  computed:{
    shortName(){
      let name = this.userLoggedIn.name

      let names = name.split(' ')

      var stName = ''

      names.forEach(s=>{
        stName += s[0]
      })
      
      return stName
    },
    getProvinceByName(){
      return this.provinces.find(province => province.province == this.selectedProvince)
    },
  },
  methods:{
    selectProvince(){
      if(this.userLoggedIn.alumni.province){
        this.selectedProvince = this.userLoggedIn.alumni.province
        this.changeProvince()
      }
    },
    selectCity(){
      if(this.userLoggedIn.alumni.city){
        this.selectedCity = this.userLoggedIn.alumni.city
      }
    },
    async getProvinces(){
      let response = await axios.get(this.BASE_URL+"/api/get-provinces")
      let data = await response.data
      this.provinces = data
      this.selectProvince()
    },
    changeProvince(){
      this.selectedCity = ""
      this.getCities()
    },
    async getCities(){
      let response = await axios.get(this.BASE_URL+"/api/get-district/"+this.getProvinceByName.province_id)
      let data = await response.data
      this.cities = data
      this.selectCity()
    },
    async editProfile(){
      this.saveLoading = true

      try{
        let data = {
          'name' : this.userLoggedIn.name,
          'email' : this.userLoggedIn.alumni.email,
          'phone' : this.userLoggedIn.email,
          'address' : this.userLoggedIn.alumni.address,
          'city' : this.selectedCity,
          'province' : this.selectedProvince,
          'country' : this.userLoggedIn.alumni.country,
          'year_in' : this.userLoggedIn.alumni.year_in,
          'class_name' : this.userLoggedIn.alumni.class_name,
          'graduation_year' : this.userLoggedIn.alumni.graduation_year,
          'date_of_birth' : this.userLoggedIn.alumni.date_of_birth,
          'private_email' : this.userLoggedIn.alumni.private_email,
          'private_phone' : this.userLoggedIn.alumni.private_phone,
          'private_domisili' : this.userLoggedIn.alumni.private_domisili,
          'skills' : this.userLoggedIn.alumni.skills
        }

        let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/edit",data)
        let newData = await response.data

        localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

        this.snackbarText = newData.message
        this.snackbar = true
        
      }catch{
        this.snackbarText = "Gagal edit profile"
        this.snackbar = true
        
      }

      this.saveLoading = false
    },
    addSkill(){
      this.userLoggedIn.alumni.skills.push({name:''})
    },
    async removeSkill(i,id){
      
      this.userLoggedIn.alumni.skills.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-skill/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    pickFile() {
      this.profileLoading = true
      this.$refs.image.click()
    },

    async onFilePicked(e) {
      
      const files = e.target.files
      if (files[0] !== undefined) {
        
        if (files[0].name.lastIndexOf('.') <= 0) {
          return
        }

        let data = new FormData();

        data.append('profile',files[0])
        data.append('phone',this.userLoggedIn.email)

        try{
          let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/upload-profile",data, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })

          let newData = await response.data
          
          this.userLoggedIn.alumni.profile_pic = newData.data.alumni.profile_pic
          
          localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

          this.snackbarText = newData.message
          this.snackbar = true
        }catch{
          this.snackbarText = "Gagal edit foto profile"
          this.snackbar = true
            
        }
      } 
      
      this.profileLoading = false

    }
  }
}
</script>