<template>
<div>

    <!-- Header Area-->
    <div class="header-area bg-success" id="headerArea">
      <div class="container h-100 d-flex align-items-center justify-content-between">
        <!-- Logo Wrapper-->
        <div class="logo-wrapper"><router-link to="/home"><img src="../assets/logo.png" alt=""></router-link></div>
        <!-- Search Form-->
        <div class="top-search-form">
          <form ref="search">
            <input class="form-control" type="search" placeholder="Enter your keyword" v-model="search">
            <button type="submit" @click="handleSearch"><i class="fa fa-search"></i></button>
          </form>
        </div>
      </div>
    </div>

<v-container class="mt-15">
    
    <v-row>
        <v-col>
            <v-snackbar v-model="snackbar" right top>
            {{ snackbarText }}
            </v-snackbar>

            <v-list subheader>

                <v-list-item v-for="dt in search ? searchData : data" :key="dt.id" three-line>
                    <v-list-item-avatar color="teal" class="justify-center">

                    <v-img

                        :alt="dt.data.user.alumni.name"
                        :src="BASE_URL+'/storage/public/'+dt.data.user.alumni.profile_pic"

                        v-if="dt.data.user.alumni.profile_pic"

                        @click="openDialog(BASE_URL+'/storage/public/'+dt.data.user.alumni.profile_pic)"

                    ></v-img>


                    <span class="white--text" v-else>{{shortName(dt.data.user.alumni.name)}}</span>

                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title v-text="dt.data.user.alumni.name"></v-list-item-title>
                        <v-list-item-subtitle v-text="dt.data.user.alumni.graduation_year"></v-list-item-subtitle>
                        <v-list-item-subtitle class="mt-3">
                            <v-chip
                                color="teal"
                                outlined
                                link
                                @click="confirmUser(dt.id,dt.data.user.id,'Diterima')"
                                text-color="teal"
                                >
                                <v-avatar left>
                                    <v-icon>mdi-check</v-icon>
                                </v-avatar>
                                Ya
                            </v-chip>
                            <v-chip
                                color="orange"
                                outlined
                                link
                                class="mx-2"
                                @click="confirmUser(dt.id,dt.data.user.id,'Ragu')"
                                text-color="orange"
                                >
                                <v-avatar left>
                                    <v-icon>mdi-close</v-icon>
                                </v-avatar>
                                Ragu - Ragu
                            </v-chip>
                            <v-chip
                                color="red"
                                outlined
                                link
                                @click="confirmUser(dt.id,dt.data.user.id,'Ditolak')"
                                text-color="red"
                                >
                                <v-avatar left>
                                    <v-icon>mdi-close</v-icon>
                                </v-avatar>
                                Tidak
                            </v-chip>
                        </v-list-item-subtitle>
                    </v-list-item-content>

                </v-list-item>

                <v-list-item v-if="data.length == 0">

                    <v-list-item-content>
                        Tidak ada Notifikasi
                    </v-list-item-content>

                </v-list-item>
                

                <v-dialog v-model="dialog" persistent max-width="400">
                    <v-card class="pt-5">
                      <v-card-text class="text-center">
                        <img :src="dialogSrc" style="width:100%;object-fit:cover;" />
                      </v-card-text>

                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          color="primary"
                          text
                          @click="dialog = false"
                        >
                          Close
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

            </v-list>

        </v-col>
    </v-row>
</v-container>

<!-- Footer Nav-->
    <div class="footer-nav-area" id="footerNav">
      <div class="container h-100 px-0">
        <div class="suha-footer-nav h-100">
          <ul class="h-100 d-flex align-items-center justify-content-between ps-0">
            <li><router-link to="/landing"><i class="lni lni-user"></i>Home</router-link></li>
            <li class="active"><router-link to="/notification"><i class="lni lni-alarm"></i>Notification</router-link></li>
            <li><router-link to="/search"><i class="lni lni-search"></i>Search</router-link></li>
            <li><a @click="logout"><i class="lni lni-power-switch"></i>Logout</a></li>
          </ul>
        </div>
      </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Search',
    data : () => ({
        data: [],
        searchData:[],
        search:'',
        BASE_URL:process.env.VUE_APP_URL,
        userLoggedIn:JSON.parse(localStorage.getItem("userLoggedIn")),
        dialog:false,
        dialogSrc:'',
        snackbar:false,
        snackbarText:''
    }),
    created(){
        this.getData()
    },
    methods:{
        async getData(){
            let response = await axios.get(this.BASE_URL+'/api/mobile/alumni/get-notifications/'+this.userLoggedIn.id)
            let data = await response.data
            this.data = data.data
        },
        shortName(fullname){
            
            let names = fullname.split(' ')

            var stName = ''

            names.forEach(s=>{
                stName += s[0]
            })
            
            return stName
        },
        handleSearch(){
            this.searchData = this.data.filter(dt=>dt.data.user.alumni.name.toLowerCase().includes(this.search.toLowerCase()))
        },
        logout(){
            localStorage.removeItem("userLoggedIn")
            this.$router.push('/login')
        },
        openDialog(src){
            this.dialogSrc = src
            this.dialog = true
        },
        async confirmUser(id,user_id,status){
            try{

                let data = {
                    id,
                    user_id,
                    friend_id:this.userLoggedIn.id,
                    status,
                }

                let response = await axios.post(this.BASE_URL+'/api/mobile/alumni/mark-as-read/',data)
                let responseStatus = await response.status

                this.snackbarText = "Berhasil "+ response.data.message +" alumni"
                this.snackbar = true

                if(responseStatus == 200){
                    this.getData()
                }
            }catch{
                this.snackbarText = "Gagal Confirm alumni"
                this.snackbar = true
            }
        }
    }
}
</script>
