<template>
  <div>
    <v-snackbar v-model="snackbar">
      {{ snackbarText }}
    </v-snackbar>

    <!-- Preloader-->
    <div class="preloader" id="preloader" v-if="loading">
      <div class="spinner-grow text-secondary" role="status">
        <div class="sr-only">Loading...</div>
      </div>
    </div>
    <!-- Login Wrapper Area-->
    <div
      class="login-wrapper d-flex align-items-center justify-content-center text-center"
      v-else
    >
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-12 col-sm-9 col-md-7 col-lg-6 col-xl-5">
            <div class="text-start px-4">
              <h5 class="mb-1 text-white">Verify Phone Number</h5>
              <p class="mb-4 text-white">
                Enter the OTP code sent to<strong class="ms-1">{{
                  postedData.phone
                }}</strong>
              </p>
            </div>
            <!-- OTP Verify Form-->
            <div class="otp-verify-form mt-5 px-4">
              <form action="#" method="">
                <div class="d-flex justify-content-between mb-5">
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp1"
                    @input="checkFilled($event)"
                  />
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp2"
                    @input="checkFilled($event)"
                  />
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp3"
                    @input="checkFilled($event)"
                  />
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp4"
                    @input="checkFilled($event)"
                  />
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp5"
                    @input="checkFilled($event)"
                  />
                  <input
                    class="form-control"
                    type="tel"
                    value=""
                    placeholder="-"
                    maxlength="1"
                    v-model="otp6"
                    @input="checkFilled($event)"
                  />
                </div>
                <button
                  class="btn btn-warning btn-lg w-100"
                  type="submit"
                  @click="handleOtp"
                >
                  Verify &amp; Proceed
                </button>
              </form>
            </div>
            <!-- Term & Privacy Info-->
            <div class="login-meta-data px-4">
              <p class="mt-3 mb-0" @click="$router.push('/login')">
                Don't received the OTP?<span
                  class="otp-sec ms-1 text-white"
                  id="resendOTP"
                ></span>
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Otp",
  data: () => ({
    loading: false,
    snackbar: false,
    snackbarText: "",
    otp1: "",
    otp2: "",
    otp3: "",
    otp4: "",
    otp5:'',
    otp6:'',
    code:"",
    validChar: false,
    postedData: JSON.parse(localStorage.getItem("postData")),
  }),
  components: {},
  methods: {
    checkFilled(event) {
      event = event ? event : window.event;
      var charCode = event.which ? event.which : event.data;

      if (event.target.value.length === 1) {
        this.nextInput(event.target);
      } else if (charCode == null) {
        this.prevInput(event.target);
      }
    },
    nextInput(el) {
      if (el.nextElementSibling) {
        var nextType = el.nextElementSibling.tagName.toLowerCase();

        if (nextType == "input") {
          el.nextElementSibling.focus();
        }
      }
    },
    prevInput(el) {
      if (el.previousSibling) {
        var nextType = el.previousSibling.tagName.toLowerCase();

        if (nextType == "input") {
          el.previousSibling.focus();
        }
      }
    },
    async getLoginData()
    {
      try{
        // var postData = {'phone':this.postedData.phone,'login':this.postedData.login,'otp':code}
        var postData = {
          phone: this.postedData.phone,
          login: this.postedData.login,
          otp: this.code,
        };

        let response = await axios.post(
          process.env.VUE_APP_URL + "/api/mobile/otp",
          postData
        );
        let data = await response.data;

        this.snackbarText = data.message;
        this.snackbar = true;

        this.loading = false;

        if (response.status == 200) {
          localStorage.setItem("userLoggedIn", JSON.stringify(data.data));

          if (data.data.alumni) {
            this.$router.push("/landing");
          } else {
            this.$router.push("/admin/alumni");
          }
        }
      } catch(err) {
        console.log(err)
        this.snackbarText = "Data tidak ditemukan";
        this.snackbar = true;
      }
    },
    async handleOtp() {
      this.loading = true;

      if (
        this.otp1 &&
        this.otp2 &&
        this.otp3 &&
        this.otp4 && this.otp5 && this.otp6
      ) {
        this.code =
          this.otp1 + this.otp2 + this.otp3 + this.otp4 + this.otp5 + this.otp6;
        
        if(typeof(Android) === "undefined")
        {
          window.confirmationResult.confirm(this.code).then( async () => {
            await this.getLoginData()
          }).catch((error) => {
            console.error(error)
          });
        }
        else
        {
          var result = Android.confirmationResult(this.code)
          if(result == this.postedData.phone){
            await this.getLoginData()
          }
          else{
            this.snackbarText = "OTP tidak sesuai";
            this.snackbar = true;
          }
        }

      } else {
        this.snackbarText = "Lengkapi otp terlebih dahulu";
        this.snackbar = true;
      }

      this.loading = false;
    },
  },
};
</script>
