<template>

  <div>
    
    <v-snackbar v-model="snackbar">
        {{ snackbarText }}
      </v-snackbar>

    <!-- Preloader-->
    <div class="preloader" id="preloader" v-if="loading">
      <div class="spinner-grow text-secondary" role="status">
        <div class="sr-only">Loading...</div>
      </div>
    </div>
    <!-- Login Wrapper Area-->
    <div class="login-wrapper d-flex align-items-center justify-content-center text-center" v-else>
      <!-- Background Shape-->
      <div class="background-shape"></div>
      <div class="container">
        <div class="row justify-content-center">
          <div class="col-12 col-sm-9 col-md-7 col-lg-6 col-xl-5"><img class="big-logo" src="../assets/logo.png" alt="">
            <!-- Register Form-->
            <div class="register-form mt-5 px-4">
              <form action="#" method="" @submit="register()">
                <div class="form-group text-start mb-4"><span>Nama</span>
                  <label for="nama"><i class="lni lni-user"></i></label>
                  <input class="form-control" id="nama" type="text" placeholder="John Doe" v-model="name" required>
                </div>
                <div class="form-group text-start mb-4"><span>Kelas</span>
                  <label for="class_name"><i class="lni lni-apartment"></i></label>
                  <input class="form-control" id="class_name" type="text" placeholder="Kelas 2" v-model="class_name" required>
                </div>
                <div class="form-group text-start mb-4"><span>Tahun Masuk</span>
                  <label for="tahun-masuk"><i class="lni lni-calendar"></i></label>
                  <select name="" id="tahun-masuk" class="form-control" v-model="year_in" required>
                    <option value="">- Pilih -</option>
                    <option v-for="year in years" :value="year" :key="year">{{ year }}</option>
                  </select>
                  <!-- <input class="form-control" id="tahun-lulus" type="text" placeholder="2020" v-model="graduation_year" required> -->
                </div>
                <div class="form-group text-start mb-4"><span>Tahun Lulus</span>
                  <label for="tahun-lulus"><i class="lni lni-calendar"></i></label>
                  <select name="" id="tahun-lulus" class="form-control" v-model="graduation_year" required>
                    <option value="">- Pilih -</option>
                    <option v-for="year in years" :value="year" :key="year">{{ year }}</option>
                  </select>
                  <!-- <input class="form-control" id="tahun-lulus" type="text" placeholder="2020" v-model="graduation_year" required> -->
                </div>
                <div class="form-group text-start mb-4"><span>No WA</span>
                  <label for="no-hp"><i class="lni lni-phone"></i></label>
                  <input class="form-control" id="no-hp" type="tel" placeholder="082345678901" v-model="phone" required>
                </div>
                <div class="form-group text-start mb-4"><span>Photo</span>
                  <input class="form-control" id="photo" type="file" accept="image/*" ref="photo" @change="handlePhotoChange" required>
                </div>
                <button class="btn btn-success btn-lg w-100 text-white" type="submit">Sign Up</button>
              </form>
            </div>
            <!-- Login Meta-->
            <div class="login-meta-data">
              <p class="mt-3 mb-0">Already have an account?<router-link class="ms-1" to="/login">Sign In</router-link></p>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SignUp',
  data : () => ({
    name:'',
    class_name:'',
    year_in:'',
    graduation_year:'',
    phone:'',
    loading:false,
    snackbar:false,
    snackbarText:'',
    photo:''
  }),
  components:{
    
  },
  methods:{
    handlePhotoChange(){
      this.photo = this.$refs.photo.files[0]
    },
    async register(){

      this.loading = true

      if(this.phone && this.name && this.graduation_year && this.year_in && this.class_name && this.photo){
        
        try{
          let data = new FormData()

          let phoneNumber = this.phone

          if(phoneNumber.startsWith(0)){
            phoneNumber = '+62'+phoneNumber.slice(1)
          }

          data.append('name',this.name)
          data.append('class_name',this.class_name)
          data.append('year_in',this.year_in)
          data.append('graduation_year',this.graduation_year)
          data.append('phone',phoneNumber)
          data.append('profile',this.photo)
  
          let response = await axios.post(process.env.VUE_APP_URL+'/api/mobile/register',data)
  
          this.loading = false
  
          this.snackbarText = response.data.message
  
          this.snackbar = true
  
          this.resetForm()
        }catch{
          this.snackbarText = "Gagal membuat data bisa disebabkan karena data sudah terdaftar, hubungi admin utk memverifikasinya."
          this.snackbar = true
          
        }

      }else{
        this.snackbarText = "Lengkapi formulir terlebih dahulu"
        this.snackbar = true
      }

      this.loading = false

    },
    resetForm(){
      this.name = ''
      this.graduation_year = ''
      this.phone = ''
    }
  },
  computed : {
    years () {
      const year = new Date().getFullYear()-4
      return Array.from({length: year - 1900}, (value, index) => year - index)
    }
  }
}
</script>