<template>
<div>

    <!-- Header Area-->
    <div class="header-area bg-success" id="headerArea">
      <div class="container h-100 d-flex align-items-center justify-content-between">
        <!-- Logo Wrapper-->
        <div class="logo-wrapper"><router-link to="/home"><img src="../assets/logo.png" alt=""></router-link></div>
        <!-- Search Form-->
        <div class="top-search-form">
          <form ref="search">
            <input class="form-control" type="search" placeholder="Enter your keyword" v-model="search">
            <button type="submit" @click="handleSearch"><i class="fa fa-search"></i></button>
          </form>
        </div>
      </div>
    </div>

<v-container class="mt-15">
    
    <v-row>
        <v-col>
            <v-list subheader>

                <v-list-item v-for="alumni in search ? searchData : data" :key="alumni.id" :to="'/search/detail/'+alumni.id">
                    <v-list-item-avatar color="teal" class="justify-center">

                    <v-img

                        :alt="alumni.name"
                        :src="BASE_URL+'/storage/public/'+alumni.profile_pic"

                        v-if="alumni.profile_pic"

                    ></v-img>

                    <span class="white--text" v-else>{{shortName(alumni.name)}}</span>

                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title v-text="alumni.name"></v-list-item-title>
                        <v-list-item-subtitle v-text="alumni.graduation_year"></v-list-item-subtitle>
                    </v-list-item-content>

                </v-list-item>
            </v-list>

        </v-col>
    </v-row>
</v-container>

<!-- Footer Nav-->
    <div class="footer-nav-area" id="footerNav">
      <div class="container h-100 px-0">
        <div class="suha-footer-nav h-100">
          <ul class="h-100 d-flex align-items-center justify-content-between ps-0">
            <li><router-link to="/landing"><i class="lni lni-user"></i>Home</router-link></li>
            <li><router-link to="/notification"><i class="lni lni-alarm"></i>Notification</router-link></li>
            <li class="active"><router-link to="/search"><i class="lni lni-search"></i>Search</router-link></li>
            <li><a @click="logout"><i class="lni lni-power-switch"></i>Logout</a></li>
          </ul>
        </div>
      </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Search',
    data : () => ({
        data: [],
        searchData:[],
        search:'',
        BASE_URL:process.env.VUE_APP_URL
    }),
    created(){
        this.getData()
    },
    methods:{
        async getData(){
            let response = await axios.get(this.BASE_URL+'/api/mobile/admin/alumni')
            let data = await response.data
            this.data = data.data
        },
        shortName(fullname){
            
            let names = fullname.split(' ')

            var stName = ''

            names.forEach(s=>{
                stName += s[0]
            })
            
            return stName
        },
        handleSearch(){
            this.searchData = this.data.filter(alumni=>alumni.name.toLowerCase().includes(this.search.toLowerCase()))
        },
        logout(){
            localStorage.removeItem("userLoggedIn")
            this.$router.push('/login')
        }
    }
}
</script>
