<style scoped>
    .ditolak{
        background:#FCE5ED;
    }
</style>

<template>
<div>

    <!-- Header Area-->
    <div class="header-area bg-success" id="headerArea">
      <div class="container h-100 d-flex align-items-center justify-content-between">
        <!-- Logo Wrapper-->
        <div class="logo-wrapper"><router-link to="/home"><img src="../../assets/logo.png" alt=""></router-link></div>
        <!-- Search Form-->
        <div class="top-search-form">
          <form ref="search">
            <input class="form-control" type="search" placeholder="Enter your keyword" v-model="search">
            <button type="submit" @click="handleSearch"><i class="fa fa-search"></i></button>
          </form>
        </div>
      </div>
    </div>

<v-container class="mt-15">
    
  <v-snackbar v-model="snackbar">
      {{snackbarText}}
    </v-snackbar>
    
    <v-row>
        <v-col>
            <v-list subheader>

                <v-list-item v-for="alumni in search ? searchData : data" :key="alumni.id" :to="'/admin/alumni/'+alumni.id" :class="alumni.user.status ? 'ditolak' : ''" :three-line="alumni.user.status">
                    <v-list-item-avatar color="teal" class="justify-center">

                    <v-img

                        :alt="alumni.name"
                        :src="BASE_URL+'/storage/public/'+alumni.profile_pic"

                        v-if="alumni.profile_pic"

                    ></v-img>

                    <span class="white--text" v-else>{{shortName(alumni.name)}}</span>

                    </v-list-item-avatar>

                    <v-list-item-content>
                        <v-list-item-title v-text="alumni.name"></v-list-item-title>
                        <v-list-item-subtitle v-text="alumni.graduation_year"></v-list-item-subtitle>
                        <v-list-item-subtitle v-if="alumni.user.status" class="red--text">Ditolak</v-list-item-subtitle>
                    </v-list-item-content>

                    <v-list-item-icon>
                        <v-chip
                            color="teal"
                            :link="alumni.approval_status == 'approved' ? false : true"
                            :outlined="alumni.approval_status == 'approved' ? false : true"
                            @click="approveAlumni(alumni.id)"
                            :text-color="alumni.approval_status == 'approved' ? 'white' : 'teal'"
                            >
                            <v-avatar left>
                                <v-icon>mdi-check</v-icon>
                            </v-avatar>
                            {{alumni.approval_status == "approved" ? "Approved" : "Approve"}}
                        </v-chip>
                    </v-list-item-icon>
                </v-list-item>
            </v-list>

        </v-col>
    </v-row>
</v-container>

<!-- Footer Nav-->
    <div class="footer-nav-area" id="footerNav">
      <div class="container h-100 px-0">
        <div class="suha-footer-nav h-100 px-5">
          <button class="btn btn-success w-100" @click="logout">Logout</button>
        </div>
      </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'Alumni',
    data : () => ({
        data: [],
        searchData:[],
        search:'',
        snackbar:false,
        snackbarText:'',
        BASE_URL:process.env.VUE_APP_URL
    }),
    created(){
        this.getData()
    },
    methods:{
        async getData(){
            let response = await axios.get(this.BASE_URL+'/api/mobile/admin/alumni')
            let data = await response.data
            this.data = data.data
        },
        shortName(fullname){
            
            let names = fullname.split(' ')

            var stName = ''

            names.forEach(s=>{
                stName += s[0]
            })
            
            return stName
        },
        async approveAlumni(id){

            try{

                let response = await axios.get(this.BASE_URL+'/api/mobile/admin/alumni/approve/'+id)
                let status = await response.status

                this.snackbarText = "Sukses approve alumni"
                this.snackbar = true

                if(status == 200){
                    this.getData()
                }
            }catch{
                this.snackbarText = "Gagal approve alumni"
                this.snackbar = true
            }
        },
        handleSearch(){
            this.searchData = this.data.filter(alumni=>alumni.name.toLowerCase().includes(this.search.toLowerCase()))
        },
        logout(){
            localStorage.removeItem("userLoggedIn")
            this.$router.push('/login')
            }
    }
}
</script>
