<style scoped>
  .profile{
    width:80px;
    height:80px;
    object-fit: cover;
    object-position: center;
  }
</style>
<template>
<div id="bg">

  <!-- Preloader-->
    <div class="preloader" id="preloader" v-if="loading">
      <div class="spinner-grow text-secondary" role="status">
        <div class="sr-only">Loading...</div>
      </div>
    </div>
    <!-- Header Area-->
    <div class="header-area" id="headerArea" v-else>
      <div class="container h-100 d-flex align-items-center justify-content-between">
        <!-- Back Button-->
        <div class="back-button"><router-link to="/profile">
            <svg class="bi bi-arrow-left" xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" viewBox="0 0 16 16">
              <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"></path>
            </svg></router-link></div>
        <!-- Page Title-->
        <div class="page-heading">
          <h6 class="mb-0">Edit Profile</h6>
        </div>
        <div></div>
      </div>
    </div>
    
    <div class="page-content-wrapper">
      <div class="container">
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-3">
          <!-- User Information-->
          <div class="card user-info-card">
            <div class="card-body p-4 d-flex align-items-center">
              <div class="user-profile me-3">

                <img :src="BASE_URL+'/storage/public/'+data.profile_pic" :alt="data.name" v-if="data.profile_pic" class="profile">
                <img src="../../assets/img/bg-img/9.jpg" alt="" v-else>

              </div>
              <div class="user-info">
                <p class="mb-0 text-dark">{{data.email ? data.email : '-'}}</p>
                <h5 class="mb-0">{{data.name}}</h5>
              </div>
            </div>
          </div>

 <!-- Single Setting Card-->
          <div class="card settings-card mt-2">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title"><i class="lni lni-protection bg-success"></i><span>Private Email</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="data.private_email">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title"><i class="lni lni-protection bg-success"></i><span>Private No HP</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="data.private_phone">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Single Setting Card-->
          <div class="card settings-card">
            <div class="card-body">
              <!-- Single Settings-->
              <div class="single-settings d-flex align-items-center justify-content-between">
                <div class="title"><i class="lni lni-protection bg-success"></i><span>Private Domisili</span></div>
                <div class="data-content">
                  <div class="toggle-button-cover">
                    <div class="button r">
                      <input class="checkbox" type="checkbox" v-model="data.private_domisili">
                      <div class="knobs"></div>
                      <div class="layer"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- User Meta Data-->
          <div class="card user-data-card">
            <div class="card-body">
              <form action="" method="">
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-user"></i><span>Full Name</span></div>
                  <input class="form-control" type="text" placeholder="Suha Jannat" v-model="data.name">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-phone"></i><span>Phone</span></div>
                  <input class="form-control" type="text" placeholder="+880 000 111 222" v-model="data.user.email">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-envelope"></i><span>Email Address</span></div>
                  <input class="form-control" type="email" placeholder="care@example.com" v-model="data.email">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-map-marker"></i><span>Domisili</span></div>
                  <input class="form-control" type="text" placeholder="Domisili" v-model="data.address">
                  <input class="form-control mt-2" type="text" placeholder="Kota" v-model="data.city">
                  <input class="form-control mt-2" type="text" placeholder="Provinsi" v-model="data.province">
                  <input class="form-control mt-2" type="text" placeholder="Negara" v-model="data.country">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-envelope"></i><span>Tahun Lulus</span></div>
                  <input class="form-control" type="text" placeholder="2004" v-model="data.graduation_year">
                </div>
                <div class="mb-3">
                  <div class="title mb-2"><i class="lni lni-envelope"></i><span>Tanggal Lahir</span></div>
                  <input class="form-control" type="date" v-model="data.date_of_birth">
                </div>
                <div class="mb-3">
                  <div class="d-flex justify-content-between">
                    <div class="title mb-2">
                      <i class="lni lni-briefcase"></i><span>Pekerjaan / Keahlian</span>
                    </div>
                    <v-btn icon small @click="addSkill">
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                  </div>
                  <div id="skills" v-for="(skill,i) in data.skills" :key="i">

                    <div class="input-group mb-3">
                      <input class="form-control" type="text" :placeholder="'Pekerjaan / Keahlian ke '+(i+1)" v-model="skill.name">
                      <div class="input-group-append">
                        <button class="btn btn-outline-secondary" type="button" @click="removeSkill(i,skill.id)"><i class="lni lni-close"></i></button>
                      </div>
                    </div>

                  </div>
                </div>
                <button class="btn btn-success w-100 text-white" type="button" @click="editProfile()">Save All Changes</button>
                <button class="btn btn-danger w-100 text-white mt-3" type="button" @click="deleteAlumni()">Delete Alumni</button>
              </form>
            </div>
          </div>

          <div class="card settings-card mt-3">
            <div class="card-body">

                <v-list subheader>
                  <v-list-item v-for="user_approve in data.user.user_approves" :key="user_approve.id">
                      <v-list-item-avatar color="teal" class="justify-center">

                      <v-img

                          :alt="user_approve.user.alumni.name"
                          :src="BASE_URL+'/storage/public/'+user_approve.user.alumni.profile_pic"

                          v-if="user_approve.user.alumni.profile_pic"

                      ></v-img>

                      <span class="white--text" v-else>{{shortName(user_approve.user.alumni.name)}}</span>

                      </v-list-item-avatar>

                      <v-list-item-content>
                          <v-list-item-title v-text="user_approve.user.alumni.name"></v-list-item-title>
                          <v-list-item-subtitle v-text="user_approve.user.alumni.graduation_year"></v-list-item-subtitle>
                      </v-list-item-content>

                      <v-list-item-icon>
                          <v-chip
                              :color="user_approve.status == 'Diterima' ? 'teal' : 'red'"
                              text-color="white"
                              >
                              <v-avatar left>
                                  <v-icon>{{user_approve.status == 'Diterima' ? 'mdi-check' : 'mdi-close' }}</v-icon>
                              </v-avatar>
                              {{user_approve.status}}
                          </v-chip>
                      </v-list-item-icon>
                  </v-list-item>
              </v-list>

            </div>
          </div>

        </div>
      </div>
    </div>

</div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'DetailAlumni',
    data : () => ({
        data: {},
        loading:false,
        snackbar:false,
        snackbarText:'',
        BASE_URL:process.env.VUE_APP_URL
    }),
    created(){
        this.getData()
    },
    methods:{
        async getData(){
            let response = await axios.get(this.BASE_URL+'/api/mobile/admin/alumni/'+this.$route.params.id)
            let data = await response.data
            this.data = data.data
        },
        async editProfile(){
            this.loading = true
          try{
            let data = {
                'name' : this.data.name,
                'email' : this.data.email,
                'phone' : this.data.user.email,
                'address' : this.data.address,
                'city' : this.data.city,
                'province' : this.data.province,
                'country' : this.data.country,
                'graduation_year' : this.data.graduation_year,
                'date_of_birth' : this.data.date_of_birth,
                'private_email' : this.data.private_email,
                'private_phone' : this.data.private_phone,
                'private_domisili' : this.data.private_domisili,
                'skills' : this.data.skills
            }

            let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/edit",data)

            this.snackbarText = "Sukses update data"
            this.snackbar = true

            if(response.status == 200){
                this.getData()
            }

          }catch{
            this.snackbarText = "Gagal update data"
            this.snackbar = true
          }

          this.loading = false
        },
        shortName(){
            
            let names = this.data.name.split(' ')

            var stName = ''

            names.forEach(s=>{
                stName += s[0]
            })
            
            return stName
        },
        addSkill(){
            this.data.skills.push({name:''})
        },
        async removeSkill(i,id){
        
            this.data.skills.splice(i,1)

            if(id){
                await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-skill/"+id)
            }

        },
        async deleteAlumni(){
            let response = await axios.get(this.BASE_URL+"/api/mobile/admin/alumni/delete/"+this.data.id)
            if(response.status == 200){
                this.$router.push('/admin/alumni')
            }
        }
    }
}
</script>
