<template>
<div id="bg">

    <!-- Header Area-->
    <div class="header-area bg-success" id="headerArea">
      <div class="container h-100 d-flex align-items-center justify-content-center">
        <!-- Logo Wrapper-->
        <div class="logo-wrapper"><router-link to="/home"><img src="../assets/logo.png" alt=""></router-link></div>
      </div>
    </div>
   
    <profile-template :privateEmail="data.private_email" :nra="userLoggedIn.alumni.NRA" :privateDomisili="data.private_domisili" :privatePhone="data.private_phone" :name="data.name" :email="data.email" :isOwn="false" :domisili="places()" :phone="data.user.email" :profile_pic="profilePic(data.profile_pic)" />

    <!-- Footer Nav-->
    <div class="footer-nav-area" id="footerNav">
      <div class="container h-100 px-0">
        <div class="suha-footer-nav h-100">
          <ul class="h-100 d-flex align-items-center justify-content-between ps-0">
            <!-- <li><router-link to="/home"><i class="lni lni-home"></i>Home</router-link></li> -->
            <li><router-link to="/profile"><i class="lni lni-user"></i>Profile</router-link></li>
            <li><router-link to="/notification"><i class="lni lni-alarm"></i>Notification</router-link></li>
            <li class="active"><router-link to="/search"><i class="lni lni-search"></i>Search</router-link></li>
            <li><a @click="logout"><i class="lni lni-power-switch"></i>Logout</a></li>
          </ul>
        </div>
      </div>
    </div>
    
</div>
</template>

<style scoped>
#bg{
  background: #e5e8ff !important;
}
</style>

<script>

import ProfileTemplate from '../components/ProfileTemplate.vue'
import axios from 'axios'

export default {
  name: 'SearchDetail',
  data: () => ({
    data: {},
    BASE_URL:process.env.VUE_APP_URL
  }),
  components:{
    ProfileTemplate
  },
  created(){
        this.getData()
    },
  methods:{
      places(){
      let user = this.data
      
      return user.address && user.city && user.province && user.country ? user.address+", "+user.city+", "+user.province+", "+user.country : '-'
    },
    shortName(){
        
        let names = this.data.name.split(' ')

        var stName = ''

        names.forEach(s=>{
            stName += s[0]
        })
        
        return stName
    },
    async getData(){
        let response = await axios.get(this.BASE_URL+'/api/mobile/admin/alumni/'+this.$route.params.id)
        let data = await response.data
        this.data = data.data
    },
    logout(){
      localStorage.removeItem("userLoggedIn")
      this.$router.push('/login')
    },
    profilePic(profile_pic){
      return process.env.VUE_APP_STORAGE_URL+'/public/'+profile_pic
    }
  }
}
</script>