<template>
<div id="bg" class="h-100">

    <v-snackbar v-model="snackbar">
        {{ snackbarText }}
      </v-snackbar>

    <!-- Preloader-->
      <div class="preloader" id="preloader" v-if="saveLoading">
        <div class="spinner-grow text-secondary" role="status">
          <div class="sr-only">Loading...</div>
        </div>
      </div>
      <!-- Header Area-->
      <div class="header-area mb-5" id="headerArea" v-else>

        <div class="container h-100 d-flex align-items-center justify-content-between">
          <!-- Back Button-->
          <div class="back-button"><router-link to="/landing">
              <svg class="bi bi-arrow-left" xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" viewBox="0 0 16 16">
                <path fill-rule="evenodd" d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"></path>
              </svg></router-link></div>
          <!-- Page Title-->
          <div class="page-heading">
            <h6 class="mb-0">QR code</h6>
          </div>

          <div>
          </div>

        </div>
        <div class="w-100 h-100 d-flex align-items-center justify-content-between">

        
        <v-tabs fixed-tabs background-color="white" color="teal" v-model="tabs">
          <v-tab href="#my-code">
            My Code
          </v-tab>
          <v-tab href="#scan-code">
            Scan Code
          </v-tab>
        </v-tabs>

        </div>
      </div>
    
    <div class="page-content-wrapper">
      <div class="container">
        <!-- Profile Wrapper-->
        <div class="profile-wrapper-area py-15">

          <v-tabs-items v-model="tabs">
            <v-tab-item value="my-code">

                <!-- Single Setting Card-->
              <div class="card settings-card text-center d-flex align-items-center justify-content-center">
                <div class="card-body text-center">
                  <!-- Single Settings-->

                  <h5>{{userLoggedIn.name}}</h5>

                  <vue-qrcode :value="kta" /> 
                  
                  <p>
                    Your QR code is private. if you share it with someone, they can scan it with their Scanner to see your KTM. 
                  </p>
                </div>
              </div>
              </v-tab-item>
              <v-tab-item value="scan-code">

                <!-- Single Setting Card-->
              <div class="card settings-card">
                <div class="card-body text-center">
                  <!-- Single Settings--> 

                  <qrcode-stream :camera="camera" @decode="onDecode"></qrcode-stream>

                  <v-dialog v-model="dialog">
                    <v-card>
                      <v-card-title class="text-h5 grey lighten-2">
                        QR Scanner
                      </v-card-title>

                      <v-card-text>
                        <div v-html="cardHtml" class="mt-5"></div>
                      </v-card-text>

                      <v-divider></v-divider>

                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          color="primary"
                          text
                          @click="dialog = false"
                        >
                          Close
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>

                </div>
              </div>

            </v-tab-item>
          </v-tabs-items>

        </div>
      </div>
    </div>
</div>
</template>

<style scoped>
    #bg{
      background: #e5e8ff !important;
    }
</style>

<script>
import axios from 'axios'
import VueQrcode from 'vue-qrcode'

import { QrcodeStream } from 'vue-qrcode-reader'

export default {
  name: 'EditProfile',
  data: () => ({
    userLoggedIn:JSON.parse(localStorage.getItem("userLoggedIn")),
    saveLoading:false,
    profileLoading:false,
    snackbar:false,
    snackbarText:'',
    BASE_URL:process.env.VUE_APP_URL,
    isForeign:false,
    provinces:[],
    cities:[],
    selectedProvince: "",
    selectedCity:"",
    tabs:'tab-1',
    qrScanned:"",
    dialog:false,
    camera: 'auto',
    showScanConfirmation: false,
    sfx: new Audio(require("@/assets/sound/scanner_sfx.mp3")),
    cardHtml:''
  }),
  components:{
    VueQrcode,
    QrcodeStream
  },
  created(){
     this.getProvinces()
  },
  computed:{
    kta(){
      return this.BASE_URL+"/api/mobile/kta/"+this.userLoggedIn.alumni.id+"&"+Math.random()
    },

    shortName(){
      let name = this.userLoggedIn.name

      let names = name.split(' ')

      var stName = ''

      names.forEach(s=>{
        stName += s[0]
      })
      
      return stName
    },
    getProvinceByName(){
      return this.provinces.find(province => province.province == this.selectedProvince)
    },
  },
  methods:{
    async onInit (promise) {
      try {
        await promise
      } catch (e) {
        console.error(e)
      } finally {
        this.showScanConfirmation = this.camera === "off"
      }
    },

    async onDecode (content) {

      this.qrScanned = content

      this.sfx.play()

      this.dialog = true

      let response = await axios.get(this.BASE_URL+"/api/mobile/kta/"+this.userLoggedIn.alumni.id+"&"+Math.random())
      let data = await response.data

      this.cardHtml = data

      this.pause()
      await this.timeout(500)
      this.unpause()
    },

    unpause () {
      this.camera = 'auto'
    },

    pause () {
      this.camera = 'off'
    },

    timeout (ms) {
      return new Promise(resolve => {
        window.setTimeout(resolve, ms)
      })
    },

    selectProvince(){
      if(this.userLoggedIn.alumni.province){
        this.selectedProvince = this.userLoggedIn.alumni.province
        this.changeProvince()
      }
    },
    selectCity(){
      if(this.userLoggedIn.alumni.city){
        this.selectedCity = this.userLoggedIn.alumni.city
      }
    },
    async getProvinces(){
      let response = await axios.get(this.BASE_URL+"/api/get-provinces")
      let data = await response.data
      this.provinces = data
      this.selectProvince()
    },
    changeProvince(){
      this.selectedCity = ""
      this.getCities()
    },
    async getCities(){
      let response = await axios.get(this.BASE_URL+"/api/get-district/"+this.getProvinceByName.province_id)
      let data = await response.data
      this.cities = data
      this.selectCity()
    },
    async editProfile(){
      this.saveLoading = true

      try{
        let data = {
          'name' : this.userLoggedIn.name,
          'email' : this.userLoggedIn.alumni.email,
          'phone' : this.userLoggedIn.email,
          'address' : this.userLoggedIn.alumni.address,
          'city' : this.selectedCity,
          'province' : this.selectedProvince,
          'country' : this.userLoggedIn.alumni.country,
          'graduation_year' : this.userLoggedIn.alumni.graduation_year,
          'date_of_birth' : this.userLoggedIn.alumni.date_of_birth,
          'private_email' : this.userLoggedIn.alumni.private_email,
          'private_phone' : this.userLoggedIn.alumni.private_phone,
          'skills' : this.userLoggedIn.alumni.skills
        }

        let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/edit",data)
        let newData = await response.data

        localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

        this.snackbarText = newData.message
        this.snackbar = true
        
      }catch{
        this.snackbarText = "Gagal edit profile"
        this.snackbar = true
        
      }

      this.saveLoading = false
    },
    addSkill(){
      this.userLoggedIn.alumni.skills.push({name:''})
    },
    async removeSkill(i,id){
      
      this.userLoggedIn.alumni.skills.splice(i,1)

      if(id){
        await axios.get(this.BASE_URL+"/api/mobile/alumni/delete-skill/"+id)
        localStorage.setItem("userLoggedIn",JSON.stringify(this.userLoggedIn))
      }

    },
    pickFile() {
      this.profileLoading = true
      this.$refs.image.click()
    },

    async onFilePicked(e) {
      
      const files = e.target.files
      if (files[0] !== undefined) {
        
        if (files[0].name.lastIndexOf('.') <= 0) {
          return
        }

        let data = new FormData();

        data.append('profile',files[0])
        data.append('phone',this.userLoggedIn.email)

        try{
          let response = await axios.post(this.BASE_URL+"/api/mobile/alumni/upload-profile",data, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })

          let newData = await response.data
          
          this.userLoggedIn.alumni.profile_pic = newData.data.alumni.profile_pic
          
          localStorage.setItem("userLoggedIn",JSON.stringify(newData.data))

          this.snackbarText = newData.message
          this.snackbar = true
        }catch{
          this.snackbarText = "Gagal edit foto profile"
          this.snackbar = true
            
        }
      } 
      
      this.profileLoading = false

    }
  }
}
</script>