import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import SignUp from '../views/SignUp.vue'
import Otp from '../views/Otp.vue'
import Profile from '../views/Profile.vue'
import Landing from '../views/Landing.vue'
import Qr from '../views/Qr.vue'
import EditProfile from '../views/EditProfile.vue'
import Search from '../views/Search.vue'
import Notification from '../views/Notification.vue'
import SearchDetail from '../views/SearchDetail.vue'

// Admin

import Alumni from '../views/admin/Alumni.vue'
import DetailAlumni from '../views/admin/DetailAlumni.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(userLoggedIn) next({name:'Landing'})
      else next()
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(userLoggedIn) next({name:'Landing'})
      else next()
    }
  },
  {
    path: '/sign-up',
    name: 'SignUp',
    component: SignUp,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(userLoggedIn) next({name:'Landing'})
      else next()
    }
  },
  {
    path: '/otp',
    name: 'Otp',
    component: Otp,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(userLoggedIn) next({name:'Landing'})
      else next()
    }
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  {
    path: '/landing',
    name: 'Landing',
    component: Landing,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  {
    path: '/qr',
    name: 'Qr',
    component: Qr,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  {
    path: '/edit-profile',
    name: 'EditProfile',
    component: EditProfile,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  {
    path: '/notification',
    name: 'Notification',
    component: Notification,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  
  {
    path: '/search',
    name: 'Search',
    component: Search,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },
  {
    path: '/search/detail/:id',
    name: 'SearchDetail',
    component: SearchDetail,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(!userLoggedIn.alumni) next({name:'Alumni'})
      else next()
    }
  },


  // admin

  {
    path: '/admin/alumni',
    name: 'Alumni',
    component: Alumni,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = JSON.parse(localStorage.getItem("userLoggedIn"))
      
      if(!userLoggedIn) next({name:'Login'})
      else if(userLoggedIn.alumni) next({name:'Landing'})
      else next()
    }
  },

  
  {
    path: '/admin/alumni/:id',
    name: 'DetailAlumni',
    component: DetailAlumni,
    beforeEnter: (to, from, next) => {
      var userLoggedIn = localStorage.getItem("userLoggedIn")
      
      if(!userLoggedIn) next({name:'Login'})
      else if(userLoggedIn.alumni) next({name:'Landing'})
      else next()
    }
  },

  { path: "*", redirect:{ name:'Home' } }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
