<template>
<div id="bg">

    <!-- Header Area-->
    <div class="header-area bg-success" id="headerArea">
      <div class="container h-100 d-flex align-items-center justify-content-center">
        <!-- Logo Wrapper-->
        <div class="logo-wrapper"><router-link to="/home"><img src="../assets/logo.png" alt=""></router-link></div>
      </div>
    </div>
    
   
    <landing-template :name="userLoggedIn.name" :nra="userLoggedIn.alumni.NRA" :email="userLoggedIn.alumni.email" :places="places" :profile_pic="profilePic()" />

    <!-- Footer Nav-->
    <div class="footer-nav-area" id="footerNav">
      <div class="container h-100 px-0">
        <div class="suha-footer-nav h-100">
          <ul class="h-100 d-flex align-items-center justify-content-between ps-0">
            <!-- <li><router-link to="/home"><i class="lni lni-home"></i>Home</router-link></li> -->
            <li class="active"><router-link to="/landing"><i class="lni lni-user"></i>Home</router-link></li>
            <li><router-link to="/notification"><i class="lni lni-alarm"></i>Notification</router-link></li>
            <li><router-link to="/search"><i class="lni lni-search"></i>Search</router-link></li>
            <li><a @click="logout"><i class="lni lni-power-switch"></i>Logout</a></li>
          </ul>
        </div>
      </div>
    </div>
    
</div>
</template>

<style scoped>
#bg{
  background: #e5e8ff !important;
}
</style>

<script>

import LandingTemplate from '../components/LandingTemplate.vue'

export default {
  name: 'Landing',
  data: () => ({
    userLoggedIn:JSON.parse(localStorage.getItem("userLoggedIn")),
    BASE_URL:process.env.VUE_APP_URL
  }),
  components:{
    LandingTemplate
  },
  computed:{
    shortName(){
      let name = this.userLoggedIn.name

      let names = name.split(' ')

      var stName = ''

      names.forEach(s=>{
        stName += s[0]
      })

      return stName
    },
    places(){
      let user = this.userLoggedIn.alumni
      
      return user.address && user.city && user.province && user.country ? user.address+", "+user.city+", "+user.province+", "+user.country : '-'
    }
  },
  created(){

  },
  methods:{
    logout(){
      localStorage.removeItem("userLoggedIn")
      this.$router.push('/login')
    },
    profilePic(){
      return process.env.VUE_APP_STORAGE_URL+'/public/'+this.userLoggedIn.alumni.profile_pic
    }
  }
}
</script>