<template>
 
    <div>

      <v-snackbar v-model="snackbar">
        {{ snackbarText }}
      </v-snackbar>

      <!-- Preloader-->
      <div class="preloader" id="preloader" v-if="loading">
        <div class="spinner-grow text-secondary" role="status">
          <div class="sr-only">Loading...</div>
        </div>
      </div>

      <!-- Login Wrapper Area-->
      <div class="login-wrapper d-flex align-items-center justify-content-center text-center" v-else>
        <!-- Background Shape-->
        <div class="background-shape"></div>
        <div class="container">
          <div class="row justify-content-center">
            <div class="col-12 col-sm-9 col-md-7 col-lg-6 col-xl-5"><img class="big-logo" src="../assets/logo.png" alt="">
              <!-- Register Form-->
              <div class="register-form mt-5 px-4">
                <form action="#" method="" ref="form">
                  <div class="form-group text-start mb-4"><span>No HP</span>
                    <label for="phone"><i class="lni lni-phone"></i></label>
                    <input class="form-control" id="phone" type="tel" v-model="phone" placeholder="+6282345678901" required>
                  </div>
                  <div class="form-check text-left text-white mb-4">
                    <input type="checkbox" class="form-check-input" id="isAdmin" v-model="isAdmin">
                    <label class="form-check-label" for="isAdmin">Login sebagai admin</label>
                  </div>

                  <template v-if="is_android">
                    <button class="btn btn-success btn-lg w-100 text-white" id="sign-in-button" type="submit" @click="login()">Log In</button>
                  </template>

                  <template v-else>
                    <div id="recaptcha-container" class="mb-3"></div>
                  </template>

                </form>

              </div>
              <!-- Login Meta-->
              <div class="login-meta-data">
                <!-- <a class="forgot-password d-block mt-3 mb-1" href="forget-password.html">Forgot Password?</a> -->
                <p class="mt-3 mb-0">Didn't have an account?<router-link class="ms-1" to="/sign-up">Register Now</router-link></p>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
</template>

<script>
// import axios from 'axios'
import { getAuth, RecaptchaVerifier,signInWithPhoneNumber } from "firebase/auth";

export default {
  name: 'Login',
  data:()=>({
    phone:'',
    loading:false,
    isAdmin:false,
    snackbar:false,
    snackbarText:'',
    is_android:true,
  }),
  created(){
    this.is_android = !(typeof(Android) === "undefined")
  },
  components:{
    
  },
  mounted() {
    if (typeof(Android) === "undefined")
    {
      const auth = getAuth();
      window.recaptchaVerifier = new RecaptchaVerifier('recaptcha-container', {
        'size': 'normal',
        'callback': () => {
          // reCAPTCHA solved, allow signInWithPhoneNumber.
          this.login()
        }
      }, auth);
      window.recaptchaVerifier.render().then((widgetId) => {
        window.recaptchaWidgetId = widgetId;
      });
    }
  },
  methods:{
    async login(){

      this.loading = true

      if(this.phone){
        try{

          let phoneNumber = this.phone
          
          if(phoneNumber.startsWith(0)){
            phoneNumber = '+62'+phoneNumber.slice(1)
          }
          

          // console.log(typeof(Android))

          if (typeof(Android) === "undefined")
          {
            const appVerifier = window.recaptchaVerifier;
            const auth = getAuth();

            signInWithPhoneNumber(auth, phoneNumber, appVerifier)
            .then((confirmationResult) => {
              // SMS sent. Prompt user to type the code from the message, then sign the
              // user in with confirmationResult.confirm(code).
              window.confirmationResult = confirmationResult;
              var postData = {'phone':phoneNumber,'login':this.isAdmin ? 'admin' : 'user'}
      
              localStorage.setItem("postData",JSON.stringify(postData))
              this.$router.push('/otp')
            }).catch((error) => {
              console.error(error)
            });
          }
          else
          {
            if(Android.signInWithPhoneNumber(phoneNumber))
            {
              var postData = {'phone':phoneNumber,'login':this.isAdmin ? 'admin' : 'user'}
        
              localStorage.setItem("postData",JSON.stringify(postData))

              this.$router.push('/otp')

            }
          }
        
          // var postData = {'phone':phoneNumber,'login':this.isAdmin ? 'admin' : 'user'}
    
          // let response = await axios.post(process.env.VUE_APP_URL+'/api/mobile/login',postData)
          // let data = await response.data
        
          // this.snackbarText = data.message
          // this.snackbar = true
    
          // if(response.status == 200){

          //   if(!data.error){
              
          //     localStorage.setItem("postData",JSON.stringify(postData))
          //     this.$router.push('/otp')

          //   }
          // }
  
        }catch(err){
          console.log(err)
          this.snackbarText = "Data tidak ditemukan"
          this.snackbar = true
          
        }
      }else{
        this.snackbarText = "Lengkapi formulir terlebih dahulu"
        this.snackbar = true
      }
      

      this.loading = false
    }
  }
}
</script>